<template>
  <div id="signup" class="text-white" @keyup.enter="signup">
    <!--  Form -->
    <!-- <v-row no-gutters> -->
      <!-- v-model="recFormValid" -->
      <div class="h3 text-center mt-5 mb-5"></div>

      <v-form ref="form" lazy-validation>
        <v-col md="6" offset-md="3">
          <v-simple-table class="info-table" dark>
            <tbody>
              <tr v-for="(item, index) in userInfo" :key="index">
                <td>{{ item.label }}</td>
                <td v-if="index === 6"> <!-- index 6 select box -->
                <v-autocomplete
                  v-model="credentials[item.value]"
                  :items="companyNameList"
                  :label="item.name"
                  hide-details
                  dense
                ></v-autocomplete>
                </td>
                <td v-else>
                  <v-text-field
                    :label="item.label"
                    :type="item.type"
                    :rules="item.label === ' ' ? passwordConfirmation : item.rule"
                    :class="item.longmsg"
                    v-model="credentials[item.value]"
                    single-line
                  ></v-text-field>
                </td>
                <td>
                  <v-btn v-if="index === 0" @click="send">   </v-btn>
                  <v-btn v-if="index === 1" @click="certified">  </v-btn>
                </td>
              </tr>
            </tbody>
          </v-simple-table>
          <br />
          <span>   </span>
          <v-card elevation="2" outlined>
                     
                  .  
                ,   
            3 2         
            .  
                
                    
                .    
            .     .  
                  .   
                .   
            . ,      
            .      .  
                     
             .
          </v-card>
          <v-checkbox
            v-model="credentials.agreed"
            label="  ."
            :rules="[value => !!value || '  .']"
            dark
          ></v-checkbox>
          <div class="d-flex justify-end"><v-btn @click="signup" large id="signUpButton" class="mb-5"></v-btn></div>
        </v-col>
      </v-form> 
      
    <!-- </v-row> -->
  </div>
</template>

<script>
import axios from "axios"
 import { mapState } from "vuex";

// const SERVER_URL = "https://localhost:8080/"
// const SERVER_URL = "https://i4a405.p.ssafy.io:8080"
const SERVER_URL = process.env.VUE_APP_SERVER_URL

export default {
  name: "Signup",
  components: {},
  computed: {
    ...mapState(["companyNameList"]),
    passwordConfirmation() {
      return [
        value => value === this.credentials.hrPassword || '  .' 
      ]
    }
  },
  data: () => ({
    userInfo: [
      {
        label: "",
        type: "text",
        value: "hrEmail",
        rule: [
          value => !!value || '  .',
          value => /^[0-9a-zA-Z]([-_.]?[0-9a-zA-Z])*@[0-9a-zA-Z]([-_.]?[0-9a-zA-Z])*.[a-zA-Z]{2,3}$/.test(value) || '   .',
        ],
        longmsg: ''
      },
      {
        label: " ",
        type: "text",
        value: "hrEmailCode",
        rule: [],
        longmsg: ''
      },
      {
        label: "",
        type: "text",
        value: "hrName",
        rule: [
          value => !!value || '  .',
        ],
        longmsg: ''
      },
      {
        label: "",
        type: "password",
        value: "hrPassword",
        rule: [
          value => !!value || '  .',
          value => /^(?=.*?[a-z])(?=.*?[0-9])(?=.*?[#?!@$%^&*-]).{8,}$/.test(value) || '  1  , ,   .'
        ],
        longmsg: 'longmsg'
      },
      {
        label: " ", 
        type: "password",
        value: "hrPasswordConfirmation",
        rule: [],
        longmsg: ''
      },
      {
        label: "",
        type: "text",
        value: "hrPhone",
        rule: [
          value => !!value || '  .',
          value => /^\d{3}-\d{3,4}-\d{4}$/.test(value) || '   .'
        ],
        longmsg: ''
      },
      {
        label: "",
        type: "text",
        value: "comName",
        rule: [
          value => !!value || '  .',
        ],
        longmsg: ''
      },
    ],
    credentials: {
      hrEmail: "",
      hrEmailCode: "",
      hrName: "",
      hrPassword: "",
      hrPasswordConfirmation: "",
      hrPhone: "",
      comName: "",
      hrCertified: "",
      agreed: false,
    },
    certifiedNum: "",
    isCertified: false,
    isPasswordCertified:false,
  }),
  created: function () {
    this.$store
      .dispatch("GET_COMPANY_NAME_LIST")
      .then(() => console.log("   "))
  },
  methods: {
    //  
    send: function() {
      
      let regexp = /^[0-9a-zA-Z]([-_.]?[0-9a-zA-Z])*@[0-9a-zA-Z]([-_.]?[0-9a-zA-Z])*.[a-zA-Z]{2,3}$/i
      if (!regexp.test(this.credentials.hrEmail)) {
        alert("  .")
        return
      }

      if (this.credentials.hrEmail) {
        axios
          .post(`${SERVER_URL}/hr/send`, this.credentials)
          .then((res) => {
            this.certifiedNum = res.data
            alert("  ")
          })
          .catch((err) => {
            console.log(err)
            alert("  ")
          })
      }
    },

    //  
    certified: function() {
      console.log(":" + this.credentials.hrEmailCode)
      console.log(":" + this.certifiedNum)
      if(!this.credentials.hrEmailCode){
        alert(" ")
      }
      else if (this.credentials.hrEmailCode == this.certifiedNum) {
        this.isCertified = true
        alert(" ")
      } else {
        alert(" ")
        this.isCertified = false
      }
    },

    signup: function() {
      if (this.$refs.form.validate()) {
        if (this.isCertified == false) {
          alert("  .")
        } else {
          let signupForm = {
            hrEmail: this.credentials.hrEmail,
            hrName: this.credentials.hrName,
            hrPassword: this.credentials.hrPassword,
            hrPhone: this.credentials.hrPhone,
            comName: this.credentials.comName
          }
          console.log(signupForm)

          axios.post(`${SERVER_URL}/hr/signup`, signupForm)
            .then(res => {
              console.log(res)
              alert(" ")
              this.$router.push({ name: "Home" })
            })
            .catch((err) => {
              console.log(err)
              alert("   .")
            })
        }
      }
    }
  }
}
</script>

<style scoped>
#signup{
  /* color: black !important; */
  background-color: #304B61;
}

#signUpButton {
  width: 20px;
}

.info-table {
  background-color: transparent !important;
}

table tbody tr:hover {
  background: inherit !important;
}

td {
  border: none !important;
}

::v-deep .longmsg > .v-input__control {
  width: min-content;
}

</style>