<template>
  <div id="schedule">
    <!-- ( )  //      . -->
    <div class="h4 mb-10">. [{{user.userComName}}] - {{user.userName}}
    </div>

    <!-- <div> -->
      <v-dialog v-model="dialog" persistent max-width="600px" @click:outside="onClickOutside">
        <template v-slot:activator="{ on, attrs }">
          <v-btn dark v-bind="attrs" v-on="on">
            <v-icon left>mdi-calendar-plus</v-icon> 
          </v-btn>
        </template>
        <v-card>
          <v-card-title>
            <span class="headline">[{{ user.userComName }}]   </span>
          </v-card-title>
          <v-card-text>
            <v-form
              ref="form"
              v-model="recFormValid"
              lazy-validation
            >
              <v-container>
                <v-row>
                  <v-col cols="12" sm="6" md="4">
                    <v-autocomplete
                      v-model="reYear"
                      :items="formData.reYear"
                      :rules="[v => !!v || '  .']"
                      label="Year"
                      required
                    >
                    </v-autocomplete>
                  </v-col>
                  <v-col cols="12" sm="6" md="4">
                    <v-autocomplete
                      v-model="reFlag"
                      :items="formData.reFlag"
                      :rules="[v => !!v || '  .']"
                      label="Season"
                      required
                    >
                    </v-autocomplete>
                  </v-col>
                  <v-col cols="12" sm="6" md="4">
                    <v-autocomplete
                      v-model="reStatus"
                      :items="formData.reStatus"
                      :rules="[v => !!v || '   .']"
                      label="Type"
                      required
                    >
                    </v-autocomplete>
                  </v-col>

                  <v-col cols="12" sm="6">
                    <v-text-field
                      v-model="dateRangeText"
                      :rules="rangeRules"
                      label=" " prepend-icon="mdi-calendar"
                      readonly
                      required
                    >
                    </v-text-field>
                    <div>  : {{ formData.reDates[0] }}</div>
                    <div>  : {{ formData.reDates[1] }}</div>
                  </v-col>
                  <v-col cols="12" sm="6">
                    <v-date-picker v-model="formData.reDates" range></v-date-picker>
                  </v-col>
                </v-row>
              </v-container>
            </v-form>
          </v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="blue darken-1" text @click="dialog = false">
              Close
            </v-btn>
            <v-btn
              color="blue darken-1"
              text
              @click="createRecruit"
            >
              Save
            </v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
      <hr>
      <div>
           . 
      </div>
      
      <v-simple-table fixed-header dark height="300px" class="rec-list mt-5">
        <thead>
          <tr>
            <th class="rec-header">No</th>
            <th class="rec-header"></th>
            <th class="rec-header"></th>
            <th class="rec-header"></th>
          </tr>
        </thead>
              <tbody >
            <!-- reSeq   .(   )  -->
            <tr v-for="(recruit, index) in getRecruitListLately" :key="index" class="text-center" @click="goToProgress(recruit, index)">
              
                <td>{{ getRecruitListCount - index }}</td>
                <!-- <td>{{ recruit.reSeq }}</td> -->
                <td>{{ recruit.reYear }} {{ recruit.reFlag }}</td>
                <td>{{ recruit.reStatus }}</td>
                <td>{{ recruit.reStartDate }} ~ {{recruit.reEndDate }}</td>              
              <!-- <td> -->
                <!-- <v-btn :to="{ name: 'Progress', params: { recruitNo: recruit.reSeq } }" ></v-btn> -->
                <!-- <v-btn @click="goToProgress(recruit)"></v-btn> -->
              <!-- </td> -->

            </tr>
            </tbody>
        
      </v-simple-table>

    <!-- </div> -->

  </div>

</template>

<script>
  import {
    mapState,
    mapGetters
  } from "vuex";

  export default {
    name: "Schedule",
    data: function () {
      return {
        // com_name: "",
        dialog: false,

        formData: {
          reYear: [2021, 2022, 2023],
          reFlag: ['', ''],
          reStatus: ['', '', '', ''],
          reDates: ['2021-01-01', '2021-01-01'],
        },
        
        recFormValid: true,
        rangeRules: [
          () => this.formData.reDates[0] <= this.formData.reDates[1] || '   .'
        ],

        reYear: null,
        reFlag: null,
        reStatus: null,
      }
    },

    methods: {
      createRecruit: function () {
        // console.log(this.$refs.form.value)
        if (this.$refs.form.validate()) {
          this.dialog = false

          let recruitForm = {
            reYear: this.reYear,
            reFlag: this.reFlag,
            reStatus: this.reStatus,
            reStartDate: this.formData.reDates[0],
            reEndDate: this.formData.reDates[1],
          }
          // console.log(recruitForm)

          this.$store.dispatch("INSERT_RECRUIT", recruitForm)
            .then(() => console.log("insertRecruit"))

        // axios.post , DB      . 
        //     (seq) , state  . 
        //this.$store.dispatch('addRecruit',    res.data)

          // this.$refs.form.reset()
        // }
        }
      },
      onClickOutside: function() {
        this.reYear = ''
        this.reFlag = ''
        this.reStatus = ''
        this.formData.reDates = ['2021-01-01', '2021-01-01']

        this.dialog = false;
      },

      goToProgress: function (recruit, index) {
        this.$store.state.selectedRecruitNo = recruit.reSeq
        this.$store.state.storeRecruitItem = recruit
        this.$store.state.selectedRecruitIndex = index
        console.log("goToProgres!!", this.$store.state.storeRecruitItem)
        this.$router.push({
          // name: 'Progress',
          name: 'Menu',
          params: {
            recruitIndex: index,
            recruitNo: recruit.reSeq,
            recruitItem: recruit
          }
        })
      },

      // mouseIn: function () {
      //   // this.backgroundColor = this.colorGrey
      // },
    },

    

    created: function () {
      this.$store
        .dispatch("GET_RECRUIT_LIST")
        .then(() => console.log("getRecruitList"))
      console.log(this.$store.state.recruitList);
    },
    computed: {
      ...mapState(["recruitList", "user"]),
      ...mapGetters(["getRecruitListLately", "getRecruitListCount"]),

      dateRangeText() {
        return this.formData.reDates.join(' ~ ')
      },

    },
  }
</script>

<style scoped>
.rec-list {
  background: transparent !important;
}
.rec-header {
  text-align: center !important;
  background: #304B61 !important;
}
table tbody tr:hover {
  background: grey !important;
  opacity: 0.9;
}
</style>