<template>
  <div id="viewers">
    <v-toolbar dark color="brown lighten-1 font-weight-bold black--text">
    <!-- <v-toolbar dark color="brown darken-1 black--text"> -->
      <v-toolbar-title></v-toolbar-title>
     
        <span class="m-2 text-subtitle-1">File:</span>
        <input
        type="file"
         id="files"
        ref="files" 
        v-on:change="handleFileUpload()" 
        multiple />
      <v-btn class="m-3" v-on:click="submitFile()"><v-icon left>mdi-table-arrow-up</v-icon> </v-btn>
      <v-btn class="m-3" @click="exportExcel"><v-icon left>mdi-download-box</v-icon>  </v-btn>
      <v-btn class="m-2" @click="updateViewerDB"><v-icon left>mdi-refresh</v-icon>  </v-btn>

    </v-toolbar>
    <v-simple-table fixed-header  height="500px" class="mt-5">
      <thead>
        <tr>
          <th class="text-center">No</th>
          <th class="text-center"></th>
          <th class="text-center"></th>
          <th class="text-center"></th>
          <th class="text-center"></th>
          <th class="text-center"></th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="viewer in comViewerList" :key="viewer.no" class="text-center">
          <td>{{ viewer.viewSeq }}</td>
          <td>{{ viewer.viewName }}</td>
          <td>{{ viewer.viewEmail }}</td>
          <td>{{ viewer.viewPhone }}</td>
          <td>{{ viewer.viewPassword }}</td>
          <td v-if="viewer.viewAssigned === 1" class="blue--text"></td>
            <td v-else class="red--text"></td>
        </tr>
      </tbody>
    </v-simple-table>
    <div class="d-flex justify-end">
      <v-btn class="m-2" v-on:click="deleteAllInterviewer"> </v-btn>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapState } from "vuex"
import XLSX from 'xlsx'
import axios from 'axios'
const SERVER_URL = process.env.VUE_APP_SERVER_URL
// const SERVER_URL = "https://localhost:8080"
  export default {
    name: "Viewers",
    props: {
      recruitItem: [Object, String, Number],
    },
    data: function () {
      return {
        tableData: [
          {
            :'1',
            : '()',
            :'email@gmail.com',
            :'CE/IM',
            :'SW',
            :'010-1111-2222',
            OR:'',
          }
        ],
        title: "",
        files: [],
      }
    },

    methods: {
      exportExcel() {
        const wb = XLSX.utils.book_new()    //    (workbook)
        const ws = XLSX.utils.json_to_sheet(this.tableData)    //   (worksheet)   
        
        XLSX.utils.book_append_sheet(wb, ws, 'sheet1')  //    

        XLSX.writeFile(wb, '_.xlsx') //  
      },

      filterdVieweeList: function () { 
        return this.comVieweeList.filter(re => re.recruitReSeq === this.recruitItem.reSeq)
      },

      submitFile() {
        let formData = new FormData()
        formData.append('title', this.title)
        formData.append('files', this.files[0])
        
        axios.post(`${SERVER_URL}/interviewer/register/` + this.recruitItem.reSeq, formData, {
          headers: {
            'Content-Type': 'multipart/form-data'
          }
        })
          .then(() => {
            console.log('   ')
            alert("   .   ")
          })
          .catch(err => {
            console.log(err)
            alert("  .    .")
            console.log('   ')
          })
      },

      handleFileUpload() {
        this.files = this.$refs.files.files
        console.log(this.files)
      },

      updateViewerDB() {

        this.$store.dispatch("GET_VIEWER_LIST",this.user.userComSeq );


      },
       deleteAllInterviewer: function() {
      if(confirm('  ?(     .)')==true){
        console.log("")
            axios.delete(`${SERVER_URL}/interviewer/delete/` + this.user.userComSeq)
            .then(() => {
              alert("   ")
              this.$store.dispatch("GET_VIEWER_LIST", this.getUserComSeq)
            })
            .catch((err) => {
              console.log(err)
              alert("   ")
            })   
      }
 },
    },
    computed: {
      ...mapState(["selectedRecruitNo", "recruitList", "comViewerList", "user"]),
      ...mapGetters(["getComViewerList", "getUserComSeq"])

    },
  }
</script>

