<template>
  <div id="caview">
    <v-row style="height: 100%">

      <v-col cols="9" class="main-box">
        <!--   -  ,  -->
        <v-row class="main-banner">
          <!--   -->
          <v-col cols="2" class="d-flex align-center">
            <h5 class="mb-0 font-weight-bold"> </h5>
          </v-col>
          <v-col cols="3"></v-col>
          <!--  -->
          <v-col cols="7" class="centering justify-start">
            <v-card
              elevation="3"
              height="70%"
              class="centering"
              :class="isViewee ? 'd-none' : ''"
              tile
            >
              <span class="subtitle-1 px-4">
                  {{ minutes }} : {{ seconds }}
              </span>
              <!-- <span v-for="(item, i) in timerBtn" :key="i">
                <v-btn v-if="item.if" color="#757575" height="100%" tile dark @click="item.click">
                  {{ item.name }}
                </v-btn>
              </span> -->
              <v-btn v-if="!timer" color="#757575" height="100%" tile dark @click="startTimer">
                
              </v-btn>
              <v-btn v-else color="#757575" height="100%" tile dark @click="stopTimer">
                
              </v-btn>
              <v-btn v-if="resetButton" color="#757575" height="100%" tile dark @click="resetTimer">
                
              </v-btn>
              <v-btn v-if="!timer" color="#757575" height="100%" tile dark @click="editTimer">
                 
              </v-btn>
              
            </v-card>
            <v-col v-if="edit" cols="2" class="d-flex pa-0" style="height: 70%">
              <v-text-field
                v-model="inputMin"
                solo
                height="100%"
                max-width="70px"
                type="number"
                label=""
                class="time-input"
                tile
                hide-details
              ></v-text-field>
              <v-text-field
                v-model="inputSec"
                solo
                height="100%"
                max-width="70px"
                type="number"
                label=""
                class="time-input"
                tile
                hide-details
              ></v-text-field>
            </v-col>
          </v-col>
        </v-row>

        <!--   - ,   -->
        <v-row class="main-screen">
          <!--  -->
          <v-col cols="4" class="viewer-box centering flex-column">
            <div v-for="i in viewer" :key="i" class="brd screen-res-sm"> {{i}}</div>
            <!-- <span v-for="sub in subscribers" :key="sub.stream.connection.connectionId">
              <user-video
                v-if="JSON.parse(sub.stream.connection.data.split('%/%')[0])['type'] !== 'viewee'"
                :stream-manager="sub" 
                @click.native="updateMainVideoStreamManager(sub)"
              />
            </span> -->
          </v-col>
          <!--  -->
          <v-col v-if="!isViewee" cols="8" class="viewee-box centering flex-wrap">
            <div v-for="i in viewee" :key="i" :class="[viewee === 1 ? `screen-res-md` : `screen-res-sm`, 'brd']"> {{i}}</div>
            <!-- <span v-for="sub in subscribers" :key="sub.stream.connection.connectionId">
            <div v-if="!isViewee">
              
            </div>
              <user-video
                v-if="JSON.parse(sub.stream.connection.data.split('%/%')[0])['type'] === 'viewee'"
                :stream-manager="sub" 
                @click.native="updateMainVideoStreamManager(sub)"
              />
            </span> -->
          </v-col>
          <v-col v-else cols="8" class="viewee-box centering flex-wrap">
            <div class="screen-res-md brd"></div>
          </v-col>
        </v-row>
      </v-col>

      <!--   -   -->
      <v-col cols="3" class="sub-box">
        <!--  -   -->
        <div :class="isViewee ? 'hidden' : ''" class="pb-2 mb-2 centering justify-space-between" style="height: 10%">
          <!--   -->
          <a-popover title="Message" trigger="click">
            <v-btn color="#757575" elevation="3" height="100%" width="35%" dark> </v-btn>
            <template slot="content">
              <v-textarea
                solo
                height="100px"
                clearable
                no-resize
                hide-details
              ></v-textarea>
              <v-btn text color="secondary">Send</v-btn>
            </template>
          </a-popover>
          <!--   -->
          <v-sheet color="white" height="100%" width="60%" elevation="3" class="d-flex justify-center align-center">
            <div>  </div>
          </v-sheet>
        </div>
        <!--  -->
        <v-card v-if="!isViewee" style="height: 90%">
          <v-tabs
            v-model="letterTab"
            slider-color="#304B61"
            class="letterTab"
            center-active
          >
            <v-tab
              v-for="(item, i) in letterList"
              :key="i"
            >
               {{ i+1 }}
            </v-tab>
          </v-tabs>
          <v-card-text class="">
            <v-tabs-items v-model="letterTab">
              <v-tab-item v-for="(item, i) in letterList" :key="i">
                {{ i+1 }}. {{ item.quest }}
                <v-divider></v-divider>
                <v-virtual-scroll
                  item-height="250"
                  :items="[item]"
                >
                  {{ item.answer }}
                </v-virtual-scroll>
              </v-tab-item>
            </v-tabs-items>
            <v-textarea
              label="Memo"
              outlined
              rows="2"
              row-height="3"
              style="margin-top: .5rem;"
              no-resize
              hide-details
            ></v-textarea>
          </v-card-text>
        </v-card>
        <v-card v-else style="height: 90%"></v-card>
        <!--  -->

      </v-col>

    </v-row>
  </div>
</template>


<script>
import { OpenVidu } from "openvidu-browser"
// import UserVideo from "@/components/room/UserVideo"
import axios from "axios"


const SERVER_URL = process.env.VUE_APP_SERVER_URL

export default {
  name: "CaView",
  components: {
    // UserVideo,
  },
  data: function () {
    return {
      isViewee: false,

      OV: undefined,
      session: undefined,
      mainStreamManager: undefined, //  
      publisher: undefined, //  
      subscribers: [],

      // 
      text: "",
      messages: [],

      // , ,  
      audioOn: true,
      audioMsg: " Off",
      videoOn: true,
      videoMsg: " Off",
      // shareOn: false,
      // shareMsg: " Off",

      // From SessionController
      sessionName: undefined,
      token: undefined,
      userName: "",
      type: undefined, //  (manager) / (interviewer) / (interviewee)

      // From Main.vue
      comName: undefined,
      re_year: undefined,
      re_flag: undefined,
      re_status: undefined,

      //   
      viewer: 2,
      viewee: 4,

      // 
      // timerBtn: [
      //   { name: '', if: !this.timer, click: this.startTimer },
      //   { name: '', if: this.timer, click: this.stopTimer },
      //   { name: '', if: this.resetButton, click: this.resetTimer },
      //   { name: ' ', if: !this.timer, click: this.editTimer },
      // ],

      timer: null,
      inputMin: 0,
      inputSec: 0,
      time: 0,
      resetButton: false,
      edit: false,

      // 
      letterList: [
        {
          'quest' : '',
          'answer' : '        .    .  ,   .'
        },
        {
          'quest' : ' ',
          'answer' : 'O say, can you see, by the dawns early light, What so proudly we hailed at the twilights last gleaming, Whose broad stripes and bright stars, through the perilous fight, Oer the ramparts we watched, were so gallantly streaming? And the rockets red glare, the bombs bursting in air, Gave proof through the night that our flag was still there. O say, does that star-spangled banner yet wave. Oer the land of the free and the home of the brave?'
        },
        {
          'quest' : ' ',
          'answer' : '   ,    .     ,  ,  .    .    .  ,   .    .     .     .  ,  .    '
        },
        {
          'quest' : ' 1000',
          'answer' : '3.'
        },
        {
          'quest' : '1   ?',
          'answer' : 'WieV! WieV!'
        },
      ],
      length: 5,
      letterTab: null,
      

    }
  },
  created: function () {
    window.addEventListener("beforeunload", this.leaveSession)
    window.addEventListener("backbutton", this.leaveSession)

    let user_data = ['comName', 're_year', 're_flag', 're_status', 'sessionName', 'token', 'userName', 'type']

    for (const data of user_data) {
      this[data] = this.$route.query[data]
    }
  },

  beforeDestroy() {
    window.removeEventListener("beforeunload", this.leaveSession)
    window.removeEventListener("backbutton", this.leaveSession)
  },

  mounted() {
    this.OV = new OpenVidu()
    this.session = this.OV.initSession()

    //   Stream 
    this.session.on("streamCreated", ({ stream }) => {
      const subscriber = this.session.subscribe(stream)
      this.subscribers.push(subscriber)
    })

    // Stream 
    this.session.on("streamDestroyed", ({ stream }) => {
      const index = this.subscribers.indexOf(stream.streamManager, 0)
      if (index >= 0) {
        this.subscribers.splice(index, 1)
      }
    })

    //   ->  
    this.session.on("signal:my-chat", (event) => {
      let message = { from: "", text: "" }
      message.from = event.from.data.split('":"')[1].slice(0, -7)
      message.text = event.data

      this.messages.push(message)
    })

    //  Stream   
    this.session.connect(this.token, { name: this.userName, type : this.type})
      .then(() => {
        let publisher = this.OV.initPublisher(undefined, {
          audioSource: undefined, // The source of audio. If undefined default microphone
          videoSource: undefined, // The source of video. If undefined default webcam
          publishAudio: true, // Whether you want to start publishing with your audio unmuted or not
          publishVideo: true, // Whether you want to start publishing with your video enabled or not
          resolution: "272x153", // The resolution of your video
          frameRate: 30, // The frame rate of your video
          insertMode: "APPEND", // How the video is inserted in the target element 'video-container'
          mirror: false, // Whether to mirror your local video or not
        })

        this.mainStreamManager = publisher
        this.publisher = publisher

        this.session.publish(this.publisher)
        this.subscribers.push(this.publisher)
      })
      .catch(err => {
        console.log("There was an error connecting to the session:", err.code, err.message)
      })

    window.addEventListener("beforeunload", this.leaveSession)
  },

  methods: {
    //   
    sendMessage() {
      if (this.text === "") return

      this.session.signal({ data: this.text, to: [], type: "my-chat" })
        .then()
        .catch(err => console.error(err))

      this.text = ""
    },

    leaveSession() {
      axios.get(`${SERVER_URL}/session/leaveSession`, {
        params: {
          sessionName: this.sessionName,
          token: this.token,
        },
      })
        .then(res => {
          console.log(res)
          if (this.session) this.session.disconnect()
          this.session = undefined
          this.mainStreamManager = undefined
          this.publisher = undefined
          this.subscribers = []
          this.OV = undefined
          window.close()
        })
        .catch(err => console.log(err))
    },

    updateMainVideoStreamManager(stream) {
      if (this.mainStreamManager === stream) {
        return this.mainStreamManager = stream
      }
    },

    audioOnOOff() {
      this.audioOn = !this.audioOn
      if (this.audioOn === true) this.audioMsg = " Off"
      else this.audioMsg = " On"

      this.publisher.publishAudio(this.audioOn)
    },

    videoOnOOff() {
      this.videoOn = !this.videoOn
      if (this.videoOn === true) this.videoMsg = " Off"
      else this.videoMsg = " On"

      this.publisher.publishVideo(this.videoOn)
    },

    handling(e) {
      if (this.moving_viewee.includes(e.key)) {
        this.moving_viewee.splice(this.moving_viewee.indexOf(e.key), 1)
      } else {
        this.moving_viewee.push(e.key)
      }

      this.visible = true
    },

    startTimer: function() {
      //1000ms = 1 second
      this.timer = setInterval(() => this.countdown(), 1000)
      this.resetButton = true
      this.edit = false
    },
    stopTimer: function() {
      clearInterval(this.timer)
      this.timer = null
      this.resetButton = true
    },
    resetTimer: function() {
      this.time = this.totalTime
      clearInterval(this.timer)
      this.timer = null
      this.resetButton = false
    },
    editTimer: function() {
      this.edit = !this.edit
    },
    padTime: function(time){
      return (time < 10 ? '0' : '') + time
    },
    countdown: function() {
      this.time--
    }
  },

  computed: {
    totalTime() {
      return Number(this.inputMin * 60) + Number(this.inputSec)
    },
    minutes: function() {
      const minutes = Math.floor(this.time / 60)
      return this.padTime(minutes)
    },
    seconds: function() {
      const seconds = this.time - (this.minutes * 60)
      return this.padTime(seconds)
    },
    questLen() {
      return this.letterList.length
    }
  },

  watch: {
    totalTime() {
      this.time = this.totalTime
    },
    
    length (val) {
      this.tab = val - 1
    },
  }
}
</script>

<style scoped>
#caview {
  height: 100%;
}
.col {
  padding: 0.5rem;
}
.row {
  margin: 0;
  --bs-gutter-x: initial;
}
.main-box {
  padding: 0;
}
.main-banner {
  height: 14%;
}
.main-screen {
  height: 86%;
}
.viewer-box {
  padding: 0;
}
.viewee-box {
  padding: 0;
  align-content: center;
}

.letterTab {
  height: 5%;
}
::v-deep .letterTab > .v-item-group {
  height: 100%;
}
.v-tab {
  min-width: initial;
  font-size: 0.7rem;
  padding: 0 0.3rem;
}

.v-list {
  height: 85%;
  width: 100%;
  overflow-y: auto;
}
.v-dialog__content {
  position: absolute;
}
::v-deep .banner .v-input, ::v-deep .banner .v-input__control {
  height: 100%;
}
::v-deep .ant-popover-inner .ant-popover-inner-content {
  padding-bottom: 0;
}

.time-input {
  height: 100%;
  border-radius: initial;
}
::v-deep .time-input > .v-input__control {
  min-height: initial;
  height: 100%;
}
::v-deep .time-input > .v-input__slot {
  padding: 0;
}
::v-deep .v-item-group > .v-slide-group__prev, ::v-deep .v-item-group > .v-slide-group__next {
  display: none;
}

::v-deep .v-input__slot > fieldset {
  height: 100%;
  top: 0;
}
::v-deep fieldset > legend {
  position: absolute;
  top: -5px;
  background: white;
}

.brd {
  border: 0.1px solid grey;
  background-color: darkgrey;
  color: white;
  font-size: 1.5rem;
  padding-top: 60px;
  text-align: center;
}
.centering {
  display: flex;
  justify-content: center;
  align-items: center;
}
.hidden {
  visibility: hidden;
}

.screen-res-sm {
  width: 288px;
  height: 162px;
}
.screen-res-md {
  width: 640px;
  height: 360px;
}


::v-deep input::-webkit-outer-spin-button,
::v-deep input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}
</style>